.page-hero{
  @extend .block;

  width: 100%;
  height: 500px;
  background-size: cover;
  background-position: center;
}


@media screen and (max-width: #{$screen-md-max}) { // max-width: 1024px
  .page-hero{
    height: 565px;
    margin-bottom: -150px;
  }
}

@media screen and (max-width: #{$screen-xs-max}) { // max-width: 590px
  .page-hero{
    height: 260px;
    margin-bottom: 0;
  }
}