.wpcf7-form{
  $text-notice-color: #C4C7D0;

  input{
    font-size: $f14px;
    font-family: $default-font;
    color: $primary-color;
    border: 1px solid $form-border-color;
    width: 100%;
    padding: 12px 21px 12px 44px;
    margin-top: 5px;


    &:focus{
      border-color: $green;
    }

    &.wpcf7-not-valid{
      border-color: $error-color;
    }

    &:not([type="submit"]){
      background-repeat: no-repeat;
      background-position: 9px center;
      background-size: 24px;
    }

    &[name="your-date"]{
      background-image: url('#{$path-to-images}form-icons/calendar.svg');
    }

    &[name="your-time"]{
      background-image: url('#{$path-to-images}form-icons/clock.svg');
    }

    &[name="your-name"],
    &[name="your-quantity"]{
      background-image: url('#{$path-to-images}form-icons/user.svg');
    }

    &[name="your-email"]{
      background-image: url('#{$path-to-images}form-icons/mail.svg');
    }

    &[name="your-tel"]{
      background-image: url('#{$path-to-images}form-icons/phone.svg');
    }
  }

  .your-name,
  .your-email,
  .your-tel,
  .your-company{
    position: relative;
    display: block;
    &:before{
      content: '';
      position: absolute;
      background-image: url('#{$path-to-images}form-icons/user.svg');
      width: 24px;
      height: 24px;
      top: 13px;
      left: 10px;
    }
  }
  .your-email{
    &:before {
      background-image: url('#{$path-to-images}form-icons/mail.svg');
    }
  }
  .your-tel{
    &:before {
      background-image: url('#{$path-to-images}form-icons/phone.svg');
    }
  }
  .your-company{
    &:before {
      background-image: url('#{$path-to-images}form-icons/company.svg');
    }
  }

  textarea{
    font-size: $f14px;
    font-family: $default-font;
    color: $primary-color;
    border: 1px solid $form-border-color;
    width: 100%;
    padding: 12px 21px;
    margin-top: 5px;
    resize: none;
    &:-moz-placeholder,
    &::-webkit-input-placeholder {
      color: #C4C4C4;
    }
    &:focus{
      border-color: $green;
    }

    &.wpcf7-not-valid{
      border-color: $error-color;
    }
  }

  label {
    @extend .flex;
    @extend .fd-column;

    font-size: $f14px;
    font-family: $secondary-font;
    color: $theme-color;
    text-transform: uppercase;
    margin: 11px 0;
    width: 100%;
  }

  .field{
    @extend .flex;
    width: 100%;

    &.field--combine{
      @extend .jc-space-between;

      label{
        width: 47%;
      }
    }
  }

  .field-notice{
    font-size: $f14px;
    font-family: $default-font;
    color: $text-notice-color;
    margin-top: 2px;
    text-align: left;
    text-transform: initial;
  }

  .wpcf7-not-valid-tip {
    @extend .absolute;

    color: $error-color;
    font-size: $f12px;
    text-transform: initial;
    bottom: -13px;
  }

  .wpcf7-submit{
    color: $white;
    background: $green;
    font-size: $f18px;
    font-weight: $bold;
    font-family: $secondary-font;
    padding: 14px 102px;
    margin-top: 20px;
    margin-bottom: 10px;
    border: 0;
    width: 100%;
    &:disabled{
      background: darkgrey;
    }

    &:hover{
      background: $light-green;
    }
  }

  .ajax-loader,.wpcf7-validation-errors{
    @extend .hidden;
  }

  @-webkit-keyframes autofill {
    to {
      color: #666;
      background-color: transparent;
      background-image: initial!important;
    }
  }

  input:-webkit-autofill {
    -webkit-animation-name: autofill;
    -webkit-animation-fill-mode: both;
  }

  .selectize-input {
    border: 1px solid #e0e0e0;
    padding: 10px 21px 10px 14px;
    display: inline-block;
    width: 100%;
    overflow: hidden;
    position: relative;
    z-index: 1;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    box-shadow: none;
    border-radius: 0;
    margin-top: 5px;

    &.focus {
      -webkit-box-shadow: none;
      box-shadow: none;
    }

    &.dropdown-active {
        -webkit-border-radius: 0;
        -moz-border-radius: 0;
        border-radius: 0;
    }
  }

  .selectize-control {
    text-transform: none;

    &.single {
      .selectize-input {
        &:after {
          content: '';
          position: absolute;
          background-image: url(../images/form-icons/vector.svg);
          background-repeat: unset;
          width: 16px;
          height: 8px;
          top: 16px;
          right: 10px;
          margin-top: unset;
          border: none;
        }
        &.dropdown-active {
          &:after {
            transform: rotate(180deg);
          }
        }
      }
    }
  }

  .wpcf7-not-valid .selectize-input {
    border-color: red;
  }

  .selectize-dropdown, .selectize-input,
  .selectize-input input {
    color: #000;
    font-family: Arimo,sans-serif;
  }

  .selectize-dropdown [data-selectable],
  .selectize-dropdown .optgroup-header {
    padding: 5px 14px;
  }

  .selectize-dropdown {
    &.active {
      background-color: #f5fafd;
      color: #495c68;
    }
  }

  .acceptance-cont{
    border: 1px solid #E0E0E0;
    padding: 24px 14px;
    margin: 19px 0 18px;
  }

  .form-checkbox {
    $label-selector: "label:before";

    label{
      @extend .flex;
      @extend .ai-center;
      font-family: $default-font;
      padding-left: 50px;
      font-style: normal;
      font-weight: normal;
      line-height: normal;
      font-size: 12px;
      color: #282828;
      text-transform: initial;
    }

    // I hate Contact form 7!!
    .wpcf7-list-item{
      margin-left: 0;
    }

    .wpcf7-list-item-label{
      //@extend .relative;
      font-size: $f20px;

      &:before {
        content: "";
        @extend .block;
        @extend .absolute;
        left: 10px;
        top: -4px;
        width: 24px;
        height: 24px;
        border: 1px solid #7AB317;
        background-color: white;
      }
    }


    input[type="checkbox"]{
      display: none;
      &:checked{
        & ~ .wpcf7-list-item-label:before{
          background-size: 16px 11px;
          background: #7AB317 url("../images/icons/check.svg") center no-repeat;
        }
      }
    }
  }
}

@media screen and (max-width: #{$screen-md-max}) { // max-width: 1024px
  .wpcf7-form{
    .acceptance-cont{
      padding: 10px 14px;
      margin: 11px 0 12px;
    }

    .wpcf7-submit{
      padding: 14px 30px;
    }
  }
}

@media screen and (max-width: #{$screen-xxs-max}) { // max-width: 320px
  .wpcf7-form{
    label{
      margin: 5px 0;
    }
  }
}