.events-previews{
  $badge-color: #FF0000;

  .item{
    .title {
      color: $black;
      font-weight: $regular;
      line-height: 30px;
      margin: 20px 0 15px;
      width: 100%;
      height: 60px;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      max-height: 60px; /* fallback */
      -webkit-line-clamp: 2; /* number of lines to show */
      -webkit-box-orient: vertical;
    }

    /* new hover effect*/
    .simple-slider-hover {
      content: '';
      @extend .absolute;
      top: -6px;
      left: -6px;
      right: -6px;
      //top: 0;
      bottom: 0;
      visibility: hidden;
      opacity: 0;
      background-color: $white;
      z-index: -1;
      -moz-transform: translate3d(0, 0, 0) scale(0.5);
      -webkit-transform: translate3d(0, 0, 0) scale(0.5);
      transform: translate3d(0, 0, 0) scale(0.5);
      -moz-box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
      -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
      -moz-transition: all .5s;
      -o-transition: all .5s;
      -webkit-transition: all .5s;
      transition: all .5s;
    }

    &:hover {
      & > .simple-slider-hover {
        visibility: visible;
        opacity: 1;
        -moz-transform: translate3d(0, 0, 0);
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
      }

      & > .thumbnail {
        box-shadow: none;
        -moz-transition: all .5s;
        -o-transition: all .5s;
        -webkit-transition: all .5s;
        transition: all .5s;
      }
    }

    .details {
      @extend .relative;
      @extend .block;
    }

    .thumbnail {
      box-shadow: $dafault-shadow;
      -moz-transition: all .3s;
      -o-transition: all .3s;
      -webkit-transition: all .3s;
      transition: all .3s;
    }

    .bg-image {
      @extend .relative;
      width: 100%; //244px;
      height: 314px;
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
    }

    .badge{
      @extend .flex;
      @extend .absolute;
      @extend .jc-center;
      @extend .ai-center;

      font-weight: $bold;
      background: $badge-color;
      color: $white;
      font-size: $f12px;
      width: 100%;
      height: 38px;
      text-transform: uppercase;
      letter-spacing: 0.1em;
      text-align: center;
    }

    .labels {
      @extend .flex;
      @extend .absolute;
      bottom: 78px;
      left: 0;
    }
    .age-label {
      @extend .svg-icon-violations;
      color: $violations-color;
      font-size: $f17px;
      text-align: center;
      line-height: 26px; // height: 42px - border: 3px*2 + padding: 5px*2
    }

    .violations-label {

    }

    //.audio-icon {
    //  @extend .absolute;
    //  left: 12px;
    //  top: 12px;
    //  //box-shadow: $dafault-shadow;
    //}

    .label{
      width: 42px;
      height: 42px;

      & + .label{
        margin-left: -3px;
      }
    }

    .description {
      color: $black;
      width: 100%;
      height: 66px;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      line-height: 22px; /* fallback */
      max-height: 66px; /* fallback */
      -webkit-line-clamp: 3; /* number of lines to show */
      -webkit-box-orient: vertical;
    }

    .date-wrap {
      @extend .flex;
      @extend .jc-space-evenly;
      @extend .relative;
      @extend .text-center;
      width: 100%;

    }

    @supports ( -ms-accelerator:true ) { // Edge hack
      /*only for edge*/
      .date-wrap {
        justify-content: space-around !important;
      }
    }
    @supports (-ms-ime-align:auto) { //  Microsoft Edge Browser 12+ (All)
      .date-wrap {
        justify-content: space-around !important;
      }
    }

    @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) { // IE11 hack
      /* IE10+ specific styles go here */
      .date-wrap {
        justify-content: space-around !important;
      }
    }

    .date, .time {
      @extend .flex;
      @extend .fd-column;
      @extend .jc-center;
      @extend .ai-center;
      @extend .absolute;
      font-family: $secondary-font;
      bottom: 0;
    }

    .date {
      color: $white;
      height: 78px;
      line-height: 33px;
      background: $theme-color;
      width: 73%;

      .day {
        font-size: $f48px;
        line-height: 35px;
      }

      .month {
        font-size: $f17px;
        line-height: 22px;
        text-transform: lowercase;
      }
    }

    .date--initial, .date--final {
      width: 74px;
    }

    .date--range-icon {
      @extend .absolute;
      background: $white;
      width: 16px;
      height: 1px;
      top: 50%;
      margin: -5px auto 0;
    }

    .time {
      font-size: $f24px;
      height: 118px;
      background: $green;
      color: $white;
      width: 27%;
      right: 0;
    }

    .location{
      @extend .flex;
      @extend .ai-center;
      color: $geo-mark-color;
      font-size: $f14px;
      margin: 15px 0 0;

      .svg-icon-geo {
        stroke: $geo-mark-color;
        stroke-width: 2px;
        width: 18px;
        height: 22px;
        margin-right: 10px;
      }
    }
  }
}

@media screen and (max-width: #{$screen-lg-max}) { // max-width: 1440px
}

@media screen and (max-width: #{$screen-md-max}) { // max-width: 1024px
  .events-previews{
    .item{
      &:hover{
        > .simple-slider-hover{
          display: none;
        }
      }

      .time{
        font-size: $f18px;
        line-height: 25px;
      }

      .title{
        font-size: $f24px;
      }
    }
  }
}

@media screen and (max-width: #{$screen-sm-max}) { // max-width: 768px

}

@media screen and (max-width: #{$screen-xs-max}) { // max-width: 590px
  .events-previews{
    .item{

      .title{
        font-size: $f24px;
      }
    }
  }

}

@media screen and (max-width: #{$screen-xxs-max}) { // max-width: 320px
}

