// Links
.link-underline {
  @extend .relative;

  &:after, &:before {
    content: "";
    width: 100%;
    margin: 0 auto;
    height: 1px;
    bottom: -2px;
    transition: all $default-transition;

    @extend .block;

    @extend .absolute;
  }

  &:after {
    left: 0;
    right: 0;
    background: transparent;
  }

  &:before {
    left: 0;
    right: 0;
    background: transparent;
  }

  &:hover {
    &:after {
      width: 0;
    }

    &:before {
      width: 100%;
    }
  }
}

.btn-default{
  font-size: $f18px;
  font-weight: $bold;
  color: $light-grey;
  background: $green;
  padding: 14px 30px;
  @extend .block;
  @extend .relative;
  @extend .o-hidden;

  &:hover{
    color: $light-grey;
    box-shadow: 0 0 100px rgba($theme-color, .1) inset;
    transition: all $default-transition;
  }
}

// Pulse after click by button
.btn {
  @extend .relative;
  @extend .o-hidden;
  transition: all $default-transition;
}

.btn:after {
  content: '';
  @extend .block;
  @extend .absolute;
  left: 50%;
  top: 50%;
  width: 260px;
  height: 260px;
  margin-left: -130px;
  margin-top: -130px;
  background: rgba($light-grey, 0.5);
  border-radius: 100%;
  opacity: .6;
  transform: scale(0);
  transition: all $default-transition;
}

.btn:not(:active):after {
  @extend .ripple;
  transition: all $default-transition;
}

/* fixes initial animation run, without user input, on page load.
 */
.btn:after {
  visibility: hidden;
}

.btn:focus:after {
  visibility: visible;
}

