.sh-collection {

  .page-content {
    display: flex;
    flex-direction: column;
    flex-flow: row wrap;
    width:100%;
    font-size: 18px;
    line-height: 26px;

    .title{
      margin-left: 0;
    }

    p {
      padding-bottom: 28px;
      line-height: 26px;
    }
  }
}


@media screen and (max-width: #{$screen-md-max}) { // max-width: 1024px
  .sh-collection{
    .page-content{
      p{
        font-size: inherit;
        line-height: inherit;
      }
    }
  }
}

@media screen and (max-width: #{$screen-xs-max}) { // max-width: 590px
.sh-collection{
  .page-content{
    font-size: 16px;

    p{
      font-size: inherit;
      line-height: inherit;
    }
  }
}
}