.modal-mask{
  $bg-color: #fff;

  -webkit-animation: slideIn 500ms cubic-bezier(.34,.34,.26,.99);
  animation: slideIn 500ms cubic-bezier(.34,.34,.26,.99);

  @extend .none;


  &.active{
    @extend .fixed;
    @extend .flex;
    @extend .ai-center;
    @extend .jc-center;
  }

  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1999;
  max-width: 100%;

  &:after {
    content: '';
    @extend .fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1998;
    background-color: rgba(11,46,89,.4);
  }

  &.leave{
    opacity: 0;
    -webkit-animation: slideOut 300ms ease-in-out;
    animation: slideOut 300ms ease-in-out;
  }


  .modal-container{
    @extend .relative;

    background: $bg-color;
    padding: 35px 25px;
    width: 400px;
    z-index: 1999;

    .btn-close{
      @extend .absolute;
      top: 15px;
      right: 15px;
      padding: 5px;
    }

    .svg-icon{
      width: 15px;
      height: 15px;
      stroke: $form-border-color;
    }

    br{
      @extend .none;
    }
    .form_wrapper{
    }
    .thank_you_box{
      display: none;
      .thanks_wrapper{
        background: #FFFFFF;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 64px 35px;
        position: relative;
        .thanks_check{
          width: 32px;
          height: 32px;
          border-radius: 50%;
          background: #7AB317 url("../images/icons/check.svg") center no-repeat;
          margin-bottom: 35px;
        }
        .thanks_text{
          line-height: 25px;
          font-size: 18px;
          text-align: center;
          color: #7AB317;
        }
        .thanks_close{
          position: absolute;
          top: -10px;
          right: 0;
          cursor: pointer;
          width: 12px;
          height: 12px;
          border-radius: 50%;
          background: url("../images/icons/grey_close.svg") center no-repeat;
        }
      }
    }
  }
}

@media screen and (max-width: #{$screen-xs-max}) { // max-width: 590px
  .modal-mask {
    z-index: 91999;

    .modal-container {
      padding: 35px 15px 15px;
      width: 330px;
    }
  }
}

@media screen and (max-width: #{$screen-xxs-max}) { // max-width: 320px
  .modal-mask {
    .modal-container {
      padding: 35px 10px 15px;
      width: 320px;
    }
  }
}