$reg_btn_width: 208px;

.page-event {

  .left-bar {
    width: 90px;
    background-color: $theme-color;
    height: 10000px;
    position: fixed;
    top: 0px;
    z-index: 1;
  }
  .social-share {
    width: 90px;

    .social_share_box {
      top: 240px;

      .share {
        width: 83px;

        &.facebook {
          background-image: url('/wp-content/themes/shevchenko/images/icons/facebook-2.svg');
        }
        &.instagram {
          background-image: url('/wp-content/themes/shevchenko/images/icons/instagram-2.svg');
        }
        &.twitter {
          background-image: url('/wp-content/themes/shevchenko/images/icons/twitter-2.svg');
          background-color: transparent;
        }
      }
    }
  }

  .slider-cont {
    padding-left: 90px;
    position: relative;

    .badge {
      $badge-color: #FF0000;

      font-family: $default-font;
      font-weight: $bold;
      font-size: 22px;
      letter-spacing: .1em;

      @extend .absolute;
      background: $badge-color;
      width: 58px;
      height: 100%;
      right: 0;
      bottom: 0;
      z-index: 0;
      text-transform: uppercase;
      color: $white;

      span {
        @extend .absolute;
        @extend .flex;
        @extend .jc-center;
        @extend .ai-center;

        top: 0;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        writing-mode: tb-rl;
        writing-mode: vertical-rl;
        transform: rotate(-180deg);
        margin-top: -45px;
      }
    }

    .details {
      position: absolute;
      top: 20%;
      left: 200px;
      color: $white;

      .title {
        font-size: 48px;
        line-height: 55px;
        font-weight: bold;
        width: 568px;
        font-family: $secondary-font;
        text-transform: uppercase;
      }
      .description {
        font-size: 16px;
        line-height: 22px;
        width: 568px;
        margin-top: 34px;
        font-family: $default-font;
      }
    }

    .nextend-arrow-previous > div,
    .nextend-arrow-next > div {
      height: 90px !important;
      width: 74px !important;

      img {
        display: none !important;
      }
    }

    .nextend-arrow-next > div, .nextend-arrow-next > div:hover {
      background: #7ab317 url(../images/icons/arrow-right.svg) center no-repeat !important;
    }

    .nextend-arrow-previous > div, .nextend-arrow-previous > div:hover {
      background: #7ab317 url(../images/icons/arrow-left.svg) center no-repeat !important;
    }

    .main-bar {
      position: absolute;
      display: block;
      height: 72px;
      bottom: 0;
      left: 148px + 90px;
      //padding-left: 90px;
      z-index: 0;
      width: calc(100% - 148px - 90px);
      background: #0B2E59;

      .info {
        width: calc(100% - 208px);
        height: 72px;
        background: #0B2E59;

        .info-bar {
          display: flex;
          align-items: center;
          justify-content: center;
          margin: auto;
          position: relative;
          height: inherit;

          .info-bar__row {
            display: flex;
            align-items: stretch;
            justify-content: center;

            > div {
              display: flex;
              color: $white;
              vertical-align: middle;
              border-left: 1px solid #fff;
              padding: 0 34px 0 33px;
              font-size: 16px;

              a {
                //color: $white !important;

                &:hover {
                  .svg-icon {
                    stroke: $green;
                  }
                }
              }

              &:first-child {
                padding-left: 0px;
                border-left: 0px;
              }

              &:last-child {
                padding-right: 0px;
                border-left: 0px;
              }

              .date-tbl {
                display: table;

                .date-row {
                  display: table-row;
                  clear: both;

                  .start, .period, .end {
                    display: table-cell;
                  }

                  .start, .end {
                    .number {
                      font-family: $secondary-font;
                      font-size: 48px;
                      float: left;
                      vertical-align: middle;
                      display: inline-block;
                      padding-right: 10px;
                    }
                    .text {
                      float: left;
                      vertical-align: middle;
                      display: inline-block;
                      font-size: 16px;
                      line-height: 20px;
                      padding-top: 7px;
                    }
                  }

                  .period {
                    vertical-align: middle;
                    padding-left: 18px;
                    padding-right: 15px;
                  }
                }
              }
            }

            .time-row {
              .svg-clock {
                width: 29px;
                height: 29px;
                stroke: #fff;
                stroke-width: 2px;
                float: left;
                margin-right: 15px;
              }
              .time {
                line-height: 19px;
                text-transform: lowercase;

                &.time--minutes {
                  display: flex;
                  align-items: center;

                  .qnt {
                    font-family: $secondary-font;
                    font-size: 48px;
                    margin-right: 10px;
                  }
                }
              }
            }

            .price {
              > span {
                vertical-align: top;
              }
              .number {
                font-family: $secondary-font;
                font-size: 48px;
                vertical-align: top;
                line-height: 42px;
              }
            }

            .prices {
              line-height: 22px;

              span {
                font-weight: $bold;
              }
            }

            .location {
              clear: both;

              a {
                display: flex;
                align-items: center;
                justify-content: center;
              }

              .svg-map-pin {
                width: 25px;
                height: 30px;
                margin-right: 15px;
                stroke: #fff;
                stroke-width: 2px;

              }

              .icon {
                background: url(../images/icons/map-marker.svg) no-repeat;
                background-size: contain;
                height: 30px;
                width: 25px;
                display: inline-block;
                margin-right: 15px;
                margin-top: 4px;
              }
              .address {

                line-height: 19px;
              }
            }

            .additional-phone {
              @extend .flex;
              @extend .ai-center;
              @extend .jc-center;

              .svg-icon-phone {
                width: 25px;
                height: 25px;
                stroke: $white;
                stroke-width: 2px;
                margin-right: 19px;
              }

              .wrap {
                .h4 {
                  text-transform: none;
                }
              }
            }

            .attendee-condition {
              display: flex;
              align-items: center;
              white-space: nowrap;
              vertical-align: middle;
              font-size: 0px;
              padding-left: 10px;

              .age-label {
                fill: #444;
                border: 3px solid #7ab317;
                width: 42px;
                height: 42px;
                padding: 5px 3px;
                background: #fff;
                color: $violations-color;
                font-size: 17px;
                text-align: center;
                line-height: 26px;
                display: inline-block;
              }

              .label {
                display: inline-block;
                margin-right: 6px;
                vertical-align: middle;
                cursor: default;

                &:last-child {
                  margin-right: 0px;
                }
              }
            }

            .info-bar__col {
              display: flex;
              align-items: center;
              justify-content: center;
            }
          }
        }
      }

      .register-btn {
        position: absolute;
        right: 0;
        bottom: 0;
        height: 90px;
        background: #7ab317;
        z-index: 1;
        color: $white;
        line-height: 90px;
        padding: 0 21px;
        font-size: 18px;
        white-space: nowrap;
        cursor: pointer;
        min-width: $reg_btn_width;
        text-align: center;

        &:disabled {
          cursor: not-allowed;
          background: $disabled-color;
        }

        &::before {
          content: "";
          display: inline-block;
          background-image: url('/wp-content/themes/shevchenko/images/icons/note.svg');
          background-repeat: no-repeat;
          background-size: contain;
          width: 28px;
          height: 22px;
          vertical-align: middle;
          margin-right: 0px;
          position: relative;
          top: -3px;
        }

        &.wanna-go {
          &::before {
            background-image: url('/wp-content/themes/shevchenko/images/icons/facebook-2.svg');
            width: 11px;
            height: 19px;
            margin-right: 4px;
            top: -2px;
          }
        }
      }
    }
  }

  .content {
    width: 100%;
    max-width: 1140px;
    padding: 100px 0px 70px 90px;
    margin: auto;

    h2 {
      font-family: $secondary-font;
      font-style: normal;
      font-weight: 700;
      line-height: 58px;
      font-size: 48px;
      text-transform: uppercase;
      color: $green;
      margin: 0 0 50px;
    }

    p {
      font-style: normal;
      font-weight: 400;
      line-height: 29px;
      margin-bottom: 30px;
      font-size: 18px;
      color: $black;
    }

    img, iframe {
      @extend .block;
      max-width: 100%;
      margin: 30px auto;
    }

    .date-range {
      @extend .flex;
      @extend .ai-center;
      color: $green;
      font-size: 18px;
      line-height: 20px;
      margin-bottom: 30px;
      text-transform: lowercase;

      .svg-calendar {
        width: 18px;
        height: 20px;
        stroke: $green;
        stroke-width: 2px;
        margin-right: 18px;
      }

      .separated-line {
        margin: 0 5px;
      }
    }

    .additional-phone {
      text-align: center;
      color: $black;
      font-family: $default-font;
      margin-top: 90px;

      .h4 {
        text-transform: none;
      }

      .wrap {
        @extend .flex;
        @extend .ai-center;
        @extend .jc-center;

        margin-top: 27px;

        .svg-icon-phone {
          width: 20px;
          height: 20px;
          stroke: $green;
          stroke-width: 2px;
          margin-right: 7px;
        }
      }
    }
  }
}

@media screen and (max-width: #{$screen-lg-max}) { // max-width: 1440px
  .page-event {
    .slider-cont {
      .main-bar {

        .info {

          .info-bar {
            .info-bar__row {
              .price {
                .number {
                  font-size: 46px;
                }
              }

              > div {
                padding: 0 10px;
                font-size: 14px;

                .date-tbl {
                  .date-row {
                    .start, .end {
                      .num {
                        font-size: 46px;
                      }
                      .text {
                        font-size: 14px;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: #{$screen-md-max}) { // max-width: 1024px
  .page-event {
    .hidden-sm{
      display: none!important;
    }
    h3, .h3{
      font-size: 22px;
    }

    .left-bar{
      width: 80px;
    }

    .info-container{
      display: block;
    }

    .slider-cont {
      padding-left: 80px;

      .n2-ss-slider-1 {
        min-height: 565px!important;
      }

      #n2-ss-10 .n2-ss-slide, #n2-ss-10 .n2-ss-layers-container{
        height: 100%!important;
      }

      .details {
        top: 20%;
        left: 120px;
        width: 75%;

        .title {
          font-size: 42px;
          line-height: 45px;
          width: 95%;
        }

        .description {
          width: 95%;
          margin-top: 30px;
        }
      }

      .main-bar {
        height: 149px;
        left: 80px;
        bottom: -149px;
        z-index: 0;
        width: calc(100% - 80px);

        .register-btn {
          bottom: 100%;
          height: 79px;
          line-height: 79px;
          padding: 0 32px;
        }

        .info{
          width: 100%;

          .info-bar{
            flex-direction: column;
            justify-content: center;
            align-items: stretch;
            height: 149px;

            .info-bar__row{
              width: 100%;
              justify-content: space-between;
              padding-left: 35px;
              padding-right: 30px;
              margin: 10px 0;

              > div{
                font-size: 16px;
                padding: 0;
                border: 0;

                .date-tbl{
                  .date-row{
                    .start, .end{
                      .number{
                        line-height: 37px;
                      }

                      .text{
                        font-size: 16px;
                        text-transform: lowercase;
                        padding: 0;
                      }
                    }
                  }
                }
              }

              .attendee-condition{
                margin-left: auto;
              }

              .line{
                width: 1px;
                height: 100%;
                background: $white;
              }
            }
          }
        }
      }

      .nextend-arrow-next, .nextend-arrow-previous {
        > div {
          height: 79px !important;
          width: 65px !important;
        }
      }

      .badge{
        width: 40px;
        font-size: 18px;
        letter-spacing: normal;
      }
    }

    .content{
      width: 92%;
      padding: 220px 0 70px 80px;

      h2{
        font-size: 42px;
        margin: 0 0 35px;
      }

      .text{
        font-size: 18px;
        line-height: 29px;
      }

      p{
        font-size: inherit;
        line-height: inherit;
      }
    }
  }
}

@media screen and (max-width: #{$screen-sm-max}) { // max-width: 768px
}

@media screen and (max-width: #{$screen-xs-max}) { // max-width: 590px
  .page-event{
    .hidden-xs{
      display: none!important;
    }

    .left-bar{
      display: none;
    }

    .main-bar-container{
      display: block;
    }
    .slider-cont{
      padding-left: 0;

      .n2-ss-slider-1 {
        min-height: 260px!important;
      }

      .nextend-arrow{
        display: none!important;
      }

      .details{
        width: 90%;
        left: 4%;
        top: 50%;
        transform: translateY(-50%);

        .title{
          width: 100%;
          font-size: 33px;
          line-height: 33px;
        }

        .description{
          display: none;
        }
      }

      .main-bar{
        position: static;
        height: auto;
        left: 0;
        bottom: auto;
        width: 100%;
        background: transparent;

        .info{
          display: flex;
          flex-direction: column;
          height: auto;

          .info-bar{
            justify-content: flex-start;
            align-items: flex-start;
            height: auto;
            width: 92%;

            .info-bar__row{
              flex-direction: column;
              align-items: flex-start;
              justify-content: flex-start;
              padding-left: 0;
              padding-right: 0;
              margin: 0;

              .attendee-condition{
                align-items: flex-start;
                padding-left: 12px;
                padding-top: 11px;
                padding-bottom: 10px;
                margin-left: 0;
              }
              .location a{
                justify-content: flex-start;
              }

              .info-bar__col{
                width: 100%;
                padding-top: 11px;
                padding-bottom: 10px;
                border-bottom: 1px solid rgba($white, 0.3);
                justify-content: flex-start;
                padding-left: 12px;
              }

              > div{
                font-size: 14px;
                &:first-child{
                  padding-left: 12px;
                }

                .date-tbl{
                  .date-row{
                    .start, .end{

                      .number{
                        font-size: 42px;
                      }

                      .text{
                        font-size: 14px;
                        line-height: 18px;
                      }
                    }
                  }
                }
              }
            }
          }
        }

        .register-btn{
          position: relative;
          width: 100%;
          height: 48px;
          line-height: 48px;
          margin-top: 2px;
        }
      }

      .badge{
        width: 100%;
        height: 40px;
        font-size: 16px;

        span{
          margin-top: 0;
          transform: rotate(0);
          writing-mode: initial;
        }
      }
    }

    .content{
      width: 92%;
      padding: 50px 0 165px 0;

      .additional-phone{
        margin-top: 50px;
      }

      h2{
        font-size: 24px;
        margin: 0 0 24px;
        line-height: 34px;
      }

      .text {
        font-size: 16px;
        line-height: 26px;
      }

      p{
        font-size: inherit;
        line-height: inherit;
      }
    }
  }
}

@media screen and (max-width: #{$screen-xxs-max}) { // max-width: 320px
}