.sh-donate-page {
  .main-content {
    .featured-image {
      width: 100%;
      height: 459px;
      background-size: cover;
    }
    .content {
      padding: 87px 10px 115px 10px;
      max-width: 1140px;
      margin: auto;
      display: flex;
      align-items: flex-start;
      width: 100%;
      font-size: 18px;
      line-height: 29px;
      color: $black;
      font-family: $default-font;

      p {
        line-height: 29px;
        margin-bottom: 29px;
      }

      .description {
        width: 62%;
        padding-right: 112px;

        > .title {
          font-family: $secondary-font;
          font-style: normal;
          font-weight: $bold;
          line-height: 58px;
          font-size: 48px;
          text-transform: uppercase;
          color: $green;
          margin: 0 0 52px;
          text-align: center;
        }
      }
      .requisites {
        width: 38%;
        padding: 62px 49px;
        box-sizing: border-box;
        background-color: $light-grey-2;
        font-size: 16px;

        > .title {
          font-family: $secondary-font;
          line-height: 28px;
          font-size: 24px;
          color: $dark-blue;
        }
      }
    }
  }
}

@media screen and (max-width: #{$screen-lg-max}) { // max-width: 1440px
}

@media screen and (max-width: #{$screen-md-max}) { // max-width: 1024px
  .sh-donate-page {
    .main-content {
      .featured-image {
        height: 584px;
      }
      .content {
        padding: 79px 10px;
        flex-direction: column;
        align-items: flex-start;
        width: 100%;
        font-size: 18px;
        line-height: 29px;
        color: $black;
        font-family: $default-font;
        .description{
          padding: 0 15%;
          width: 100%;
          p{
            font-weight: normal;
            line-height: 29px;
            font-size: 18px;
            color: #282828;
            padding: 0;
          }
          >.title{
            font-style: normal;
            font-weight: bold;
            line-height: 58px;
            font-size: 42px;
            letter-spacing: 0.04em;
            margin: 0 0 39px;
          }
        }
        .requisites{
          width: 534px;
          margin: 110px auto;
          padding: 47px 58px;
        }
      }
    }
  }
}

@media screen and (max-width: #{$screen-sm-max}) { // max-width: 768px
}

@media screen and (max-width: #{$screen-xs-max}) { // max-width: 590px
  .sh-donate-page {
    .main-content {
      .featured-image {
        height: 263px;
      }
      .content {
        padding: 50px 0 0;
        flex-direction: column;
        align-items: flex-start;
        width: 100%;
        font-size: 18px;
        line-height: 29px;
        color: $black;
        font-family: $default-font;
        .description{
          padding: 0 17px;
          width: 100%;
          p{
            font-style: normal;
            font-weight: normal;
            line-height: 26px;
            font-size: 16px;
          }
          >.title{
            font-style: normal;
            font-weight: bold;
            line-height: normal;
            font-size: 24px;
            letter-spacing: 0.02em;
            text-transform: uppercase;
            margin: 0 0 25px;
            text-align: left;
          }
        }
        .requisites{
          width: 100%;
          margin: 65px auto 0;
          padding: 42px 17px;
        }
      }
    }
  }
}

@media screen and (max-width: #{$screen-xxs-max}) { // max-width: 320px
}