.excursions-list{
  width: 100%;
  //@extend .relative;

  .item{
    @extend .flex;
    @extend .fd-row;
    @extend .jc-space-between;
    @extend .relative;
    width: 100%;
    padding-left: 7%;
    margin-bottom: 35px;

    .title{
      color: $theme-color;
      font-weight: $regular;
      line-height: 30px;
      margin: 20px 0 25px;
      width: 100%;
      //height: 60px;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      max-height: 60px;      /* fallback */
      -webkit-line-clamp: 2; /* number of lines to show */
      -webkit-box-orient: vertical;

      a:hover{
        color: $green;
      }
    }

    .labels{
      @extend .flex;

      .label{

        &:not(:last-child) {
          margin-right: 10px;
        }
      }

      .age-label{
        @extend .svg-icon-violations;
        color: $violations-color;
        font-size: $f17px;
        text-align: center;
        line-height: 26px; // height: 42px - border: 3px*2 + padding: 5px*2
      }
    }

    .description{
      color: $black;
      font-size: $f14px;
      line-height: 19px;
      padding-right: 30px;
      margin-bottom: 20px;
    }

    .location{
      @extend .flex;
      color: $geo-mark-color;

      .svg-icon-geo {
        stroke: $geo-mark-color;
        stroke-width: 2px;
        width: 18px;
        height: 22px;
        margin-right: 10px;
      }
    }

    .bg-image{
      @extend .relative;
      width: 408px; //244px;
      height: 272px;
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;

      a:hover{
        color: inherit;
      }
    }

    .overlay {
      @extend .absolute;
      @extend .flex;
      @extend .jc-space-between;
      @extend .ai-center;
      bottom: 0;
      left: 0;
      width: 100%;
      background: $theme-color;
      padding: 8px;
    }

    .tickets-info{
      @extend .flex;
      @extend .ai-center;

      .icon-ticket{
        display: block;
        width: 32px;
        height: 32px;
        margin-right: 10px;
        background: url('#{$path-to-images}ticket-icon.png');
      }

      .tickets-prices{
        color: $white;
        font-size: $f14px;
        line-height: 19px;
      }

      .price{
        span{
          font-weight: $bold;
        }
      }
    }

    /* new hover effect 1*/
    .simple-slider-hover {
      content: '';
      @extend .absolute;
      top: -20px;
      left: 40px;
      right: -20px;
      bottom: -20px;
      visibility: hidden;
      opacity: 0;
      background-color: $white;
      z-index: -1;
      -moz-transform: translate3d(0, 0, 0) scale(0.5);
      -webkit-transform: translate3d(0, 0, 0) scale(0.5);
      transform: translate3d(0, 0, 0) scale(0.5);
      -moz-box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
      -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
      -moz-transition: all .5s;
      -o-transition: all .5s;
      -webkit-transition: all .5s;
      transition: all .5s;
    }

    &:hover {
      & > .simple-slider-hover {
        visibility: visible;
        opacity: 1;
        -moz-transform: translate3d(0, 0, 0);
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
      }
    }
  }
}

@media screen and (max-width: #{$screen-lg-max}) { // max-width: 1440px
}

@media screen and (max-width: #{$screen-md-max}) { // max-width: 1024px
  .excursions-list{
    .item{
      padding-left: 0;

      &:hover {
        & > .simple-slider-hover {
          display: none;
        }
      }

      .simple-slider-hover{
        display: none;
      }

      .title{
        margin: 0 0 15px;
        font-size: $f24px;
      }

      .bg-image{
        width: 374px;
      }

      .overlay{
        //position: relative;
      }

      .labels {
        position: absolute;
        top: -42px;
        right: 0;

        .label{
          &:not(:last-child){
            margin-right: -2px;
          }
        }
      }

      .thumbnail {
        margin-left: 35px;
      }

      .description{
        padding-right: 0;
        margin-bottom: 10px;
      }
    }
  }
}

@media screen and (max-width: #{$screen-sm-max}) { // max-width: 768px
}

@media screen and (max-width: #{$screen-xs-max}) { // max-width: 590px
  .excursions-list{
    .item{
      margin-bottom: 45px;
      flex-direction: column-reverse;

      .thumbnail{
        margin-left: 0;
      }

      .bg-image{
        width: 100%;
      }

      .overlay{
        padding: 14px;
      }

      .title{
        margin: 17px 0 14px;
        font-size: 22px;
      }

      .description{
        display: none;
      }
    }
  }
}

@media screen and (max-width: #{$screen-xxs-max}) { // max-width: 320px
}