#searchform,
#main_searchform{
  color: #979797;
  opacity: 0;
  @extend .pe-none;
  @extend .animated;
  @extend .absolute;

  width: 0;
  background: $theme-color;
  top: 50%;
  transform: translateY(-50%);
  //left: 18%;
  //left: 260px;
  //right: 588px;
  right: -25px;
  padding: 10px 15px 10px 15px;
  transition: width $default-transition;

  &.show{
    @extend .pe-auto;
    //@extend .fadeInDown;
    width: 50vw;
    //top: 16%;
    opacity: 1;
    transition: width $default-transition;
  }

  &.hide{
    //@extend .fadeOutUp;
    //width: 0;
    //opacity: 0;
    //transition: width .3s ease-in-out, visibility .3s ease-in-out;
  }

  .btn-search-icon{
    @extend .absolute;
    left: 14px;
    top: 55%;
    transform: translateY(-50%);
  }

  /* form starting stylings ------------------------------- */
  .group{
    @extend .relative;
  }

  input{
    @extend .block;
    font-size: 18px;
    padding: 11px 45px 10px;
    color: #979797;
    background: white;
    width: 100%;
    border: none;

    &:focus{
      outline: none;
    }
  }

  /* LABEL ======================================= */

  label{
    color: #979797;
    font-size: 14px;
    position: absolute;
    pointer-events: none;
    left: 45px;
    top: 50%;
    transform: translateY(-50%);
    //transition: $ease-transition;
  }

  /* active state */
  input{
    &:focus ~ label{
      //transition: $ease-transition;
    }
    &:valid ~ label{
      opacity: 0;
      //transition: $ease-transition;
    }
  }

   /* BOTTOM BARS ================================= */
  .bar{
    @extend .relative;
    @extend .block;
    width: 100%;

    &:before,
    &:after{
      content: '';
      height: 2px;
      width: 0;
      bottom: 0;
      position: absolute;
      background: $white;
      transition: $default-transition;
    }

    &:before{
      left: 50%
    }

    &:after{
      right: 50%
    }
  }


  /* active state */
  input:focus{
    ~ .bar:before,
    ~ .bar:after{
      width: 50%
    }
  }

  .search_close{
    cursor: pointer;
    position: absolute;
    //top: 10px;
    //right: 10px;
    //width: 20px;
    //height: 25px;

    top: 50%;
    right: 5px;
    transform: translateY(-50%);
    width: 35px;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    svg{
      stroke: #E0E0E0;
      width: 12px;
      height: 12px;
    }
  }

}
.search-form{
  position: relative;
}
.wpcf7-form {
  .btn_submit_real {
    display: none;
  }
}

.invisible-recaptcha {

  .grecaptcha-badge {
    display: none;
  }

  .recaptcha-badge {
    text-align: center;
    font-size: 0;
    position: fixed;
    bottom: 0;
    z-index: 1;
    background-color: #F3F6F9;
    left: 0;
    padding: 10px;
    transform: scale(0.5);
    transform-origin: bottom left;
    opacity: 0;
    transition: all $default-transition;

    .logo {
      display: inline-block;
      background: url('https://www.gstatic.com/recaptcha/api2/logo_48.png') no-repeat;
      width: 44px;
      height: 44px;
      background-size: contain;
    }
    .text {
      font-size: 12px;
      line-height: 16px;
    }
    .subtext {
      display: inline-block;
      font-size: 0;
      a {
        font-size: 10px;
        text-decoration: none;
        color: #4b4c4c;
        display: inline-block;
        margin-right: 3px;
        &:last-child {
          margin-right: 0;
        }
      }
    }
  }
  &.visible {
    .recaptcha-badge {
      opacity: 0.5;

      &:hover {
        transform: scale(1);
        opacity: 1;
      }
    }
  }
}

@media screen and (max-width: #{$screen-lg-max}) { // max-width: 1440px
  #searchform{
    &.show{
      width: 50vw;
      right: -5px;
    }
  }
}

@media screen and (max-width: #{$screen-md-max}) { // max-width: 1024px
  .search-form{
    position: static;
  }
  #searchform {
    padding: 24px;

    &.show{
      width: 100%;
      //top: 0;
      right: 0;
      z-index: 10;
    }
  }
}

@media screen and (max-width: #{$screen-sm-max}) { // max-width: 768px
}

@media screen and (max-width: #{$screen-xs-max}) { // max-width: 590px
  #searchform{
    padding: 9px 10px;
    input{
      font-size: 14px;
      padding: 13px 45px 13px;
    }
  }
}

@media screen and (max-width: #{$screen-xxs-max}) { // max-width: 320px
