.events-upcoming {
  border-bottom: 1px solid $light-grey;

  .container {
    max-width: $events-previews-width;
  }

  .title {
    font-family: $secondary-font;
    text-transform: uppercase;
  }
  &.related{
    padding-bottom: 48px;
    .h2{
      text-align: center;
    }
  }
  .h2 {
    color: $theme-color;
    margin-left: 26px;
    line-height: $f44px;
  }

  .wrap {
    @extend .flex;
    @extend .jc-start;
    @extend .relative;
    flex-wrap: wrap;
    margin-bottom: 70px;
  }

  .item {
    //flex: auto 1;
    @extend .relative;
    width: 24.5%;
    margin-bottom: 38px;
    padding: 22px 22px;

    &:first-child {
      //background: $teaser-bg;
      //border: 3px solid $green;
    }

    &:not(:nth-child(4n)) {
      margin-right: 0.666%;
    }

    .notice {
      margin-left: 26px;
      margin-bottom: 50px;
    }
  }
}

@media screen and (max-width: #{$screen-lg-max}) { // max-width: 1440px

}

@media screen and (max-width: #{$screen-md-max}) { // max-width: 1024px
  .events-upcoming{
    .container{
      max-width: 78%;
    }

    .h2{
      margin-top: 50px;
      margin-left: 22px;
      line-height: 50px;
    }

    .item{
      width: 48.5%;
      margin-right: 0.666%;

      &:nth-child(2n){
        margin-right: 0;
      }
    }

    .wrap{
      margin-bottom: 0;
    }
  }
}

@media screen and (max-width: #{$screen-sm-max}) { // max-width: 768px

}

@media screen and (max-width: #{$screen-xs-max}) { // max-width: 590px
  .events-upcoming{
    border-bottom: 0;

    .container{
      max-width: 90%;
    }

    .h2{
      line-height: 33px;
      margin-left: 0;
      text-align: center;
    }

    .item{
      width: 100%;
      margin-right: 0!important;
      margin-bottom: 10px;
    }

    .wrap{
      width: 292px;
      margin: 0 auto;
    }
  }

}

@media screen and (max-width: #{$screen-xxs-max}) { // max-width: 320px
}