$main_max_width: 1140px;

.sh-visiting-page {
  .main-content {
    .content {
      padding: 66px 0;

      > .title {
        font-family: $secondary-font;
        font-style: normal;
        font-weight: $bold;
        line-height: 58px;
        font-size: 48px;
        text-transform: uppercase;
        color: $green;
        margin: 0 0 14px 0;
        text-align: center;
      }

      .tabs {
        width: 100%;
        max-width: $main_max_width;
        display: table;
        margin: 0 auto 69px auto;

        .wrapper {
          display: table-row;
          .tab {
            display: table-cell;
            width: 33.3333%;
            vertical-align: bottom;
            text-transform: uppercase;
            font-size: 24px;
            line-height: 28px;
            color: $black;
            font-family: $secondary-font;
            text-align: center;
            cursor: pointer;
            border-bottom: 3px solid transparent;
            transition: border-bottom $default-transition;
            position: relative;
            z-index: 2;
            padding: 38px 0 5px;

            &:hover, &.active {
              border-bottom: 3px solid $green;
              z-index: 3;
            }
          }
        }

        position: relative;

        &:after {
          content: ' ';
          display: block;
          height: 100%;
          top: 0px;
          left: -5000px;
          width: 10000px;
          background: $white;
          z-index: 1;
          position: absolute;
        }
        &:before {
          content: ' ';
          display: block;
          height: 1px;
          width: 100%;
          bottom: 0px;
          left: 0px;
          background: $form-border-color;
          z-index: 2;
          position: absolute;
        }

        &.sticky-content-navigation {
          position: sticky;
          z-index: 2;
        }
      }

      .addresses {
        width: 100%;
        max-width: $main_max_width;
        display: table;
        margin: 0px auto 82px auto;
        font-size: 0;
        text-align: center;

        .item {
          width: 33.3333%;
          min-width: 320px;
          display: inline-block;
          vertical-align: top;
          padding-left: 11px;
          padding-right: 11px;

          .img {
            width: 100%;
            height: 310px;
            background-size: cover;
            background-repeat: no-repeat;
          }

          .name {
            width: 100%;
            height: 120px;
            text-align: center;
            font-size: 24px;
            line-height: 28px;
            color: #fff;
            background-color: $theme-color;
            font-family: $secondary-font;
            display: flex;
            flex-direction: row;
            align-items: center;
            padding: 0 40px;
          }

          .details {
            width: 100%;
            height: 420px;
            border-left: 1px solid $form-border-color;
            border-right: 1px solid $form-border-color;
            padding: 30px 40px;

            .details-tbl {
              width: 100%;
              display: table;

              .row {
                display: table-row;

                .col {
                  display: table-cell;
                  vertical-align: top;
                  text-align: left;

                  &.col_icon {
                    width: 65px;

                    .icon {
                      stroke: $green;
                      stroke-width: 2;
                      position: relative;
                      top: 5px;

                      &.icon_location {
                        height: 22px;
                        width: 18px;
                      }
                      &.icon_phone {
                        height: 20px;
                        width: 20px;
                      }
                      &.icon_timetable {
                        height: 20px;
                        width: 20px;
                      }
                      &.icon_parking {
                        height: 40px;
                        width: 48px;
                      }
                    }
                  }

                  &.col_text {
                    font-size: 16px;
                    line-height: 22px;
                    color: $black;
                    font-family: $default-font;
                    padding-bottom: 26px;

                    a {
                      text-decoration: underline;
                      color: $green;

                      &:hover {
                        color: $light-green;
                      }
                    }
                  }
                }
              }
            }
          }

          .routes {
            width: 100%;
            display: flex;
            height: 64px;
            flex-direction: row;
            align-items: flex-start;

            .route {
              background-color: $green;
              flex-grow: 1;
              height: 100%;
              margin-right: 2px;
              text-align: center;
              height: 64px;
              width: 25%;
              transition: background-color $default-transition;

              &:hover {
                background-color: $light-green;
              }

              a {
                display: inline-block;
                width: 100%;
                height: 100%;
              }

              &_walk {
                .svg {
                  width: 25px;
                  height: 38px;
                  position: relative;
                  top: 14px;
                }
              }

              &_car {
                .svg {
                  width: 48px;
                  height: 31px;
                  position: relative;
                  top: 19px;
                }
              }

              &_transport {
                .svg {
                  width: 36px;
                  height: 40px;
                  position: relative;
                  top: 12px;
                }
              }

              &_bicycle {
                .svg {
                  width: 43px;
                  height: 39px;
                  position: relative;
                  top: 14px;
                }
              }

              &:last-child {
                margin-right: 0px;
              }
            }
          }
        }
      }

      $marker_icon_w: 40px;
      $marker_icon_h: 56px;
      $marker_hint_w: 250px;
      .neighbour-activities {
        width: 100%;
        margin-bottom: 136px;

        .map {
          width: 100%;
          margin-bottom: 56px;
          position: relative;
          font-size: 0px;

          .marker {
            position: absolute;
            top: 0px;
            left: 0px;
            cursor: pointer;
            width: 1px;
            height: 1px;

            // eto mojet ispolzovatsia kak tochka v otnoshenii kotoroi idiot pozitsyonirovanie markera
            // background: red;

            .hint-wrapper {
              width: 100%;
              position: relative;
              top: -1 * $marker_icon_h;
              left: -1 * $marker_icon_w / 2;

              .hint {
                width: $marker_hint_w;
                font-size: 12px;
                text-align: center;
                position: absolute;
                bottom: 20px;
                left: -104px;
                padding: 10px 20px;
                line-height: 17px;
                color: $black;

                box-shadow: 0 3px 9px rgba(0,0,0,0.1);
                background: #fff;

                &:after {
                  display: block;
                  content: "";
                  width: 0;
                  height: 0;
                  border-style: solid;
                  border-width: 11px 12.5px 0 12.5px;
                  border-color: #fff transparent transparent transparent;
                  position: absolute;
                  bottom: -11px;
                  left: 111px;
                }
              }




              &.right {
                top: 0px;
                left: $marker_icon_w;
                .hint {
                  left: 0px;
                  &:after {
                    transform: rotate(90deg) !important;
                    left: -18px !important;
                    bottom: 12px !important;
                  }
                }
              }
              &.left {
                top: 0px;
                left: -1 * ($marker_icon_w + $marker_hint_w);
                .hint {
                  left: 0px;
                  &:after {
                    transform: rotate(-90deg) !important;
                    left: ($marker_hint_w - 7px) !important;
                    bottom: 12px !important;
                  }
                }
              }


            }

            .svg-map-marker {
              width: $marker_icon_w;
              height: $marker_icon_h;
              fill: $green;
              position: relative;
              top: -1 * $marker_icon_h;
              left: -1 * $marker_icon_w / 2;
              transform-origin: center bottom;
              -webkit-filter: drop-shadow( 0px 0px 4px rgba(79, 96, 51, 0.3) );
              filter: drop-shadow( 0px 0px 4px rgba(79, 96, 51, 0.3) );
            }

            &.active, &:hover {
              z-index: 1;
              .hint-wrapper {
                display: block !important;
                top: -1 * $marker_icon_h - 12px;

                &.right {
                  top: 0px;
                  left: $marker_icon_w + 5px;
                }
                &.left {
                  top: 0px;
                  left: -1 * ($marker_icon_w + $marker_hint_w + 5px);
                }
              }

              .svg-map-marker {
                fill: $light-green;
                transform: scale(1.2);
              }
            }

            &.big {
              .svg-map-marker {
                transform: scale(1.8);
              }

              &.active, &:hover {
                .hint-wrapper {
                  top: -1 * $marker_icon_h - 57px;

                  &.right {
                    top: -25px;
                    left: $marker_icon_w + 23px;
                  }
                  &.left {
                    top: -25px;
                    left: -1 * ($marker_icon_w + $marker_hint_w + 23px);
                  }
                }

                .svg-map-marker {
                  transform: scale(2);
                }
              }
            }
          }
        }

        .items {
          width: 100%;
          max-width: $main_max_width;
          display: table;
          margin: 0 auto 136px auto;

          .item {
            display: table-cell;
            width: 33.3333%;
            vertical-align: top;
            text-align: center;

            .icon-cont {
              background-color: $green;
              display: inline-block;
              width: 109px;
              height: 64px;
              margin: auto;
              text-align: center;
              line-height: 96px;
              margin-bottom: 47px;

              .icon {
                width: 45px;
                height: 43px;
              }
            }

            .title {
              font-family: $secondary-font;
              font-size: 24px;
              line-height: 28px;
              color: $theme-color;
              text-transform: uppercase;
              margin-bottom: 37px;
            }

            .places {
              .place {
                font-size: 16px;
                line-height: 22px;
                margin-bottom: 15px;
                font-family: $default-font;
                color: $black;
                cursor: pointer;
                transition: color $default-transition;

                &:hover {
                  color: $green;
                }
              }
            }
          }
        }
      }

      .book-tour {
        width: 100%;
        max-width: $main_max_width;
        margin: auto;

        .images {
          font-size: 0px;
          margin-bottom: 72px;
          text-align: center;

          .image {
            display: inline-block;
            width: 33.3333%;
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center center;
            vertical-align: top;
            height: 394px;
          }
        }

        .wrapper {
          font-size: 0;

          .details {
            text-align: center;
            width: 50%;
            display: inline-block;
            vertical-align: top;

            .icon-cont {
              background-color: $green;
              display: inline-block;
              width: 109px;
              height: 64px;
              margin: auto;
              text-align: center;
              line-height: 60px;
              margin-bottom: 47px;

              .icon {
                position: relative;
                width: 64px;
                height: 58px;
              }
            }
            .title {
              text-transform: uppercase;
              font-size: 24px;
              line-height: 30px;
              font-family: $secondary-font;
              color: $theme-color;
              height: 70px;
            }
            .text {
              font-size: 16px;
              line-height: 30px;
              color: $black;
              font-family: $default-font;

              .icon-phone {
                stroke: $green;
                stroke-width: 2;
                width: 20px;
                height: 20px;
                position: relative;
                top: 5px;
                margin-right: 11px;
              }

              a {
                text-decoration: underline;
                color: $green;

                &:hover {
                  color: $light-green;
                }
              }
            }

            &.details_guides {
              .icon-cont {
                .icon {
                  top: 3px;
                }
              }
            }
          }
        }
      }
    }
  }
}

