$main_max_width: 1140px;

.sh-contacts-page {

  .main-content {
    padding: 64px 0;
    width: 100%;
    //max-width: $main_max_width;
    max-width: 1300px; //a bit wider to keep content readable
    display: flex;
    flex-direction: row;
    margin-left: auto;
    margin-right: auto;

    .content {
      width: 100%;
      vertical-align: top;
      padding: 0 50px 0 0;

      > .title {
        font-family: $secondary-font;
        font-style: normal;
        font-weight: $bold;
        line-height: 58px;
        font-size: 48px;
        text-transform: uppercase;
        color: $green;
        margin: 0 0 67px 0;
        text-align: left;
      }

      .locations {
        .location {
          display: table;
          width: 100%;
          margin-bottom: 68px;

          > .row {
            display: table-row;

            > .col {
              display: table-cell;
              vertical-align: top;

            }

            > .col_image {
              width: 306px;
              margin-right: 28px;

              .image {
                width: 306px;
                height: 404px;
                background-size: cover;
                background-repeat: no-repeat;
                background-position: center center;
                margin-right: 28px;
              }
            }

            > .col_info {

              > .title {
                font-size: 24px;
                line-height: 30px;
                text-transform: uppercase;
                color: $theme-color;
                font-family: $secondary-font;
                margin-bottom: 40px;
              }

              .details-tbl {
                width: 100%;
                display: table;

                .row {
                  display: table-row;

                  .col {
                    display: table-cell;
                    vertical-align: top;
                    text-align: left;

                    &.col_icon {
                      width: 40px;

                      .icon {
                        stroke: $green;
                        stroke-width: 2;
                        position: relative;
                        top: 2px;

                        &.icon_location {
                          height: 22px;
                          width: 18px;
                        }
                        &.icon_phone {
                          height: 20px;
                          width: 20px;
                        }
                        &.icon_envelope {
                          height: 24px;
                          width: 24px;
                          top: 0;
                        }
                        &.icon_timetable {
                          height: 20px;
                          width: 20px;
                        }
                      }
                    }

                    &.col_text {
                      font-size: 16px;
                      line-height: 22px;
                      color: $black;
                      font-family: $default-font;

                      .text {
                        padding-bottom: 26px;
                        min-height: 69px;

                        a {
                          text-decoration: underline;
                          color: $green;

                          &:hover {
                            color: $light-green;
                          }
                        }
                      }

                    }
                  }
                }
              }

            }

          }

          &:last-child {
            margin-bottom: 0px;
          }
        }
      }

      .addresses {
        width: 100%;
        max-width: $main_max_width;
        display: table;
        margin: 0px auto 82px auto;
        font-size: 0;
        text-align: center;

        .item {
          width: 33.3333%;
          min-width: 320px;
          display: inline-block;
          vertical-align: top;
          padding-left: 11px;
          padding-right: 11px;

          .img {
            width: 100%;
            height: 310px;
            background-size: cover;
            background-repeat: no-repeat;
          }

          .name {
            width: 100%;
            height: 120px;
            text-align: center;
            font-size: 24px;
            line-height: 28px;
            color: #fff;
            background-color: $theme-color;
            font-family: $secondary-font;
            display: flex;
            flex-direction: row;
            align-items: center;
            padding: 0 40px;
          }

          .details {
            width: 100%;
            height: 420px;
            border-left: 1px solid $form-border-color;
            border-right: 1px solid $form-border-color;
            padding: 30px 40px;

            .details-tbl {
              width: 100%;
              display: table;

              .row {
                display: table-row;

                .col {
                  display: table-cell;
                  vertical-align: top;
                  text-align: left;

                  &.col_icon {
                    width: 65px;

                    .icon {
                      stroke: $green;
                      stroke-width: 2;
                      position: relative;
                      top: 5px;

                      &.icon_location {
                        height: 22px;
                        width: 18px;
                      }
                      &.icon_phone {
                        height: 20px;
                        width: 20px;
                      }
                      &.icon_timetable {
                        height: 20px;
                        width: 20px;
                      }
                      &.icon_parking {
                        height: 40px;
                        width: 48px;
                      }
                    }
                  }

                  &.col_text {
                    font-size: 16px;
                    line-height: 22px;
                    color: $black;
                    font-family: $default-font;
                    padding-bottom: 26px;

                    a {
                      text-decoration: underline;
                      color: $green;

                      &:hover {
                        color: $light-green;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }

    .form-contacts-sidebar {
      background: #FFFFFF;
      width: 400px;
      min-width: 400px;
      margin-top: 120px;
      h2.title{
        background: #0B2E59;
        font-style: normal;
        font-weight: normal;
        line-height: normal;
        font-size: 24px;
        text-align: center;
        color: #FFFFFF;
        .contact_icon{
          position: relative;
          text-transform: initial;
          padding: 26px 0 28px 40px;
          display: inline-block;
          &:before{
            content: '';
            background: url("../images/icons/contact_icon.svg") center no-repeat;
            background-size: contain;
            position: absolute;
            top: 25px;
            left: -12px;
            display: block;
            width: 32px;
            height: 32px;
          }
        }
      }
      .textwidget{
        border: 1px solid #E0E0E0;
        border-top: 0;
        box-sizing: border-box;
        padding: 32px 24px 16px;
      }

      .acceptance-cont{
        border: 1px solid #E0E0E0;
        padding: 24px 14px;
        margin: 41px 0 18px;
        span{
          display: inline-block;
        }
      }

      .form-checkbox {
        $label-selector: "label:before";

        label{
          @extend .flex;
          @extend .ai-center;
          font-family: $default-font;
          padding-left: 50px;
          font-style: normal;
          font-weight: normal;
          line-height: normal;
          font-size: 12px;
          color: #282828;
          text-transform: none;
        }

        // I hate Contact form 7!!
        .wpcf7-list-item{
          margin-left: 0;
        }

        .wpcf7-list-item-label{
          //@extend .relative;
          font-size: $f20px;

          &:before {
            content: "";
            @extend .block;
            @extend .absolute;
            left: 10px;
            top: 7px; //some compromise for IE and Chrome, not very centered
            width: 24px;
            height: 24px;
            border: 1px solid #7AB317;
            background-color: white;
          }
        }


        input[type="checkbox"]{
          display: none;
          &:checked{
            & ~ .wpcf7-list-item-label:before{
              background-size: 16px 11px;
              background: #7AB317 url("../images/icons/check.svg") center no-repeat;
            }
          }
        }
      }

    }
    .thank_you_box{
      display: none;
      .thanks_wrapper{
        width: 400px;
        background: #FFFFFF;
        border: 1px solid #F2F2F2;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 64px 35px;
        position: relative;
        .thanks_check{
          width: 32px;
          height: 32px;
          border-radius: 50%;
          background: #7AB317 url("../images/icons/check.svg") center no-repeat;
          margin-bottom: 35px;
        }
        .thanks_text{
          line-height: 25px;
          font-size: 18px;
          text-align: center;
          color: #7AB317;
        }
        .thanks_close{
          position: absolute;
          top: 21px;
          right: 23px;
          cursor: pointer;
          width: 12px;
          height: 12px;
          border-radius: 50%;
          background: url("../images/icons/grey_close.svg") center no-repeat;
        }
      }
    }


  }
}

@media screen and (max-width: #{$screen-lg-max}) { // max-width: 1440px
}

@media screen and (max-width: #{$screen-md-max}) { // max-width: 1024px
  .sh-contacts-page{
    .main-content{
      flex-direction: column;
      align-items: center;
      padding: 40px;
    }
  }
}

@media screen and (max-width: #{$screen-sm-max}) { // max-width: 768px
  .sh-contacts-page{
    .main-content{
      padding: 75px 12%;
      .content{
        padding: 0;
        >.title{
          font-style: normal;
          font-weight: bold;
          line-height: 58px;
          font-size: 42px;
          letter-spacing: 0.04em;
          margin: 0 0 62px;
        }
        .locations{
          .location{
            margin-bottom: 49px;
            >.row{
              display: flex;
              flex-direction: column;
              >.col_image{
                width: 100%;
                .image{
                  width: 100%;
                  max-height: 404px;
                  margin: 0;
                }
              }
              >.col_info{
                >.title{
                  font-style: normal;
                  font-weight: normal;
                  line-height: 30px;
                  font-size: 24px;
                  text-transform: uppercase;
                  color: #0B2E59;
                  margin: 47px 0 44px;
                }
                .details-tbl{
                  .row{
                    .col.col_text{
                      line-height: 26px;
                    }
                    .col.col_icon {
                      width: 68px;
                      .icon{
                        left: 10px;
                        &.icon_location{
                          width: 22px;
                          height: 30px;
                        }
                        &.icon_envelope{
                          top: 2px;
                        }
                        &.icon_phone{
                          height: 22px;
                          width: 30px;
                        }
                        &.icon_timetable{
                          width: 24px;
                          height: 24px;
                        }
                      }
                    }
                  }
                }
              }
            }

          }

        }
      }
      .form-contacts-sidebar {
        width: 90%;
        margin-top: 102px;
        .textwidget{
          padding: 32px 88px 16px;
        }
      }
    }
  }
}

@media screen and (max-width: #{$screen-xs-max}) { // max-width: 590px
  .sh-contacts-page{
    .main-content{
      padding: 43px 5%;
      .content{
        padding: 0;
        >.title{
          font-style: normal;
          font-weight: bold;
          line-height: normal;
          font-size: 24px;
          letter-spacing: 0.02em;
          margin: 0 0 33px;
        }
        .locations{
          .location{
            margin-bottom: 26px;
            >.row{
              display: flex;
              flex-direction: column;
              >.col_image{
                width: 100%;
                height: 285px;
                .image{
                  width: 100%;
                  max-height: 285px;
                  margin: 0;
                }
              }
              >.col_info{
                >.title{
                  font-style: normal;
                  font-weight: normal;
                  line-height: 30px;
                  font-size: 22px;
                  margin: 23px 0 28px;
                }
                .details-tbl{
                  .row{
                    .col.col_icon {
                      width: 43px;
                      .icon{
                        left: 0;
                        .icon_location{
                          width: 22px;
                          height: 30px;
                        }
                        .icon_phone{
                          height: 22px;
                          width: 30px;
                        }
                        .icon_timetable{
                          width: 24px;
                          height: 24px;
                        }
                      }
                    }
                  }
                }
              }
            }

          }

        }
      }
      .form-contacts-sidebar {
        width: 100%;
        min-width: 100%;
        margin-top: 43px;
        h2.title{
          .contact_icon{
            padding: 16px 0 19px 40px;
            &:before{
              top: 16px;
              left: 0;
            }
          }
        }
        .textwidget{
          padding: 30px 0 16px;
          border: 0;
        }
        .acceptance-cont{
          border: 1px solid #e0e0e0;
          padding: 24px 14px;
          margin: 9px 0 24px;
        }
      }
    }
  }
}

@media screen and (max-width: #{$screen-xxs-max}) { // max-width: 320px
}