.sh-event-request {

  .main-content {

    &>.content h2{
      margin-left: inherit;
      text-align: left;
    }

    .form-contacts-sidebar .textwidget {
      border: 1px solid #e0e0e0;
      box-sizing: border-box;
      padding: 32px 24px 16px;
    }

    .thank_you_box{
      display: none;
      .thanks_wrapper{
        width: 400px;
        background: #FFFFFF;
        border: 1px solid #F2F2F2;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 64px 35px;
        position: relative;
        .thanks_check{
          width: 32px;
          height: 32px;
          border-radius: 50%;
          background: #7AB317 url("../images/icons/check.svg") center no-repeat;
          margin-bottom: 35px;
        }
        .thanks_text{
          line-height: 25px;
          font-size: 18px;
          text-align: center;
          color: #7AB317;
        }
        .thanks_close{
          position: absolute;
          top: 21px;
          right: 23px;
          cursor: pointer;
          width: 12px;
          height: 12px;
          border-radius: 50%;
          background: url("../images/icons/grey_close.svg") center no-repeat;
        }
      }
    }
  }

  .page-content-row {
    display: flex;
    flex-flow: row wrap;
  }
  .page-content-col {
    width:65%;
    display: flex;
    flex-direction: column;
    padding-right: 96px;

    p {
      padding-bottom: 28px;
      line-height: 26px;
    }
  }
  .request-form-col {
    width:35%;
    display: flex;
    //flex: 1 0 0%;
    flex-direction: column;
  }

  .form-contacts-sidebar {

    .acceptance-cont{
      border: 1px solid #E0E0E0;
      padding: 24px 14px;
      margin: 41px 0 18px;
    }

    .form-checkbox {
      $label-selector: "label:before";

      label{
        @extend .flex;
        @extend .ai-center;
        font-family: $default-font;
        padding-left: 50px;
        font-style: normal;
        font-weight: normal;
        line-height: normal;
        font-size: 12px;
        color: #282828;
        text-transform: initial;
      }

      // I hate Contact form 7!!
      .wpcf7-list-item{
        margin-left: 0;
      }

      .wpcf7-list-item-label{
        //@extend .relative;
        font-size: $f20px;

        &:before {
          content: "";
          @extend .block;
          @extend .absolute;
          left: 10px;
          top: -4px;
          width: 24px;
          height: 24px;
          border: 1px solid #7AB317;
          background-color: white;
        }
      }

      input[type="checkbox"]{
        display: none;
        &:checked{
          & ~ .wpcf7-list-item-label:before{
            background-size: 16px 11px;
            background: #7AB317 url("../images/icons/check.svg") center no-repeat;
          }
        }
      }
    }
  }

  .sh-download-file {
    .sh-download-file__button {
      color: #0B2E59;
      text-decoration: underline;
    }
    .icon-application {
      content: ' ';
      background-image: url("../images/icons/application.svg");
      background-position: center;
      background-repeat: no-repeat;
      width: 30px;
      height: 30px;
      display: inline-block;
      vertical-align: middle;
      margin-right: 14px;
    }
    .sh-download-file__size {
      color: #C4C7D0;
      font-size: 14px;
      margin-left: 10px;
    }
  }
}

@media screen and (max-width: #{$screen-lg-max}) { // max-width: 1440px
}

@media screen and (max-width: #{$screen-md-max}) { // max-width: 1024px
  .sh-event-request {
    .page-content-col {
      padding-right: 0;
      width: 100%;

    }
    .form-contacts-sidebar {
      width: 400px;
    }

    .request-form-col {
      align-items: center;
    }

    .request-form-col {
      width: 100%;
      padding: 80px 0 0;
    }
  }
}

@media screen and (max-width: #{$screen-sm-max}) { // max-width: 768px
  .sh-event-request {
    .form-contacts-sidebar {
      width: 100%;
    }
  }
}

@media screen and (max-width: #{$screen-xxs-max}) { // max-width: 320px
  .sh-event-request {
    .form-contacts-sidebar {
      width: 100%;
    }
  }
}