#events-bar {
  padding-top: 11px;
}

.events-filter {
  width: 250px;
  //width: 260px;
  //width: 100%;

  .btn-default{
    width: 100%;
  }

  .hidden-field{
    @extend .absolute;
    @extend .pe-none;
    opacity: 0;
    width: 0;
    height: 0;

  }

  input {
    border-bottom: 1px solid $theme-color;
  }

  .heading {
    font-family: $secondary-font;
    font-size: $f24px;
    color: $theme-color;
  }
}

#filter-date {
  label{
    @extend .block;

    &:hover{
      cursor: pointer;
    }
  }

  .date-range {
    font-family: $secondary-font;
    margin: 20px auto 45px;

    .column{
      @extend .relative;
      border-style: solid;
      border-width: 1px;
      border-color: $form-border-color;
      border-radius: 4px;
      padding: 5px;
      width: 120px;
      transition: all $default-transition;

      &:hover, &.active{
        border-color: $green;
        transition: all $default-transition;

        & > .label{
          color: $green;
        }

        & > .calendar-icon{
          background-color: $green;
        }
      }
    }

    .label{
      @extend .absolute;
      top: 0;
      left: 0;
      padding: 5px;
      background: $white;
      transform: translate(20%, -50%);
      color: $black;
      transition: all $default-transition;
    }

    .calendar-icon {
      @extend .absolute;
      bottom: 1px;
      left: -1px;
      //background: url('#{$path-to-images}icons/calendar.svg') center no-repeat rgba($green, 0.87);
      background: url('#{$path-to-images}icons/calendar.svg') center no-repeat $form-border-color;
      width: calc(100% + 2px);
      height: 30px;
      transform: translateY(100%);
      transition: all $default-transition;
    }
  }

  .date-range {
    @extend .flex;
    @extend .jc-space-between;
  }

  .column {
    @extend .flex;
    @extend .jc-center;
    @extend .ai-center;
  }

  .num {
    font-size: $f40px;
    color: $green;
    margin-right: 3px;
  }

  .month, .day {
    font-size: $f13px;
    text-transform: lowercase;
  }

  .month {
    color: $theme-color;
  }

  .day {
    color: $default-color;
  }

  .cell:first-child {
    margin-right: 10px;
  }
  // options test
  .options {
    @extend .flex;
    @extend .jc-space-between;
  }

  .btn-group{
    margin-bottom: 50px;

    ul{
      @extend .flex;
      @extend .jc-space-between;
      @extend .relative;

      background: $white;
      border: 1px solid $form-border-color;
      //padding: 12px 22px;
      font-size: $f14px;
      color: $black;

      &:before{
        content: "";
        @extend .absolute;
        top: -35%;
        left: calc(50% - 1px);
        width: 1px;
        background: $form-border-color;
        height: 85%;
        transform: translate(50%, 50%);
        z-index: 9;
      }

      li{
        width: 100%;
        height: 100%;
        text-align: center;
        transition: all $default-transition;

        &:hover{
          cursor: pointer;
          color: $white;
          background: $green;
          transition: all $default-transition;
        }

        label{
          padding: 12px 0;
        }
      }
    }


    $label-selector: "~ ul > li";

    input[name="period-control"]{
      @for $i from 1 through 2 {
        &:nth-of-type(#{$i}):checked {
          #{$label-selector}:nth-child(#{$i}) {
            color: $white;
            background: $green;
            transition: all $default-transition;
          }
        }
      }
    }

  }
}

#filter-venues, #filter-subcategories {
  margin-bottom: 50px;

  .option{
    margin-top: 20px;
  }

  .checkbox {
    input:checked {
      & ~ .checkbox-label {
        &:before {
          background: url('#{$path-to-images}icons/check.png') no-repeat center;
          border-color: $green;
          background-color: $green;
          transition: background-color $default-transition, border-color $default-transition;
        }
      }
    }
  }

  .checkbox-label {
    @extend .relative;
    @extend .block;
    padding-left: 40px;
    color: $black;
    transition: $default-transition;

    &:hover {
      cursor: pointer;
      color: $green;
      transition: $default-transition;
    }

    span {
      @extend .block;
      line-height: 22px;

      &:first-child {
        font-weight: $bold;
      }
    }

    &:before {
      content: "";
      @extend .block;
      @extend .absolute;
      left: 0;
      top: 2px;
      width: 21px;
      height: 21px;
      border: 1px solid $form-border-color;
      transition: background-color $default-transition, border-color $default-transition;
    }
  }
}

@media screen and (max-width: #{$screen-lg-max}) { // max-width: 1440px

}

@media screen and (max-width: #{$screen-md-max}) { // max-width: 1024px
  #events-bar{
    position: fixed;
    top: 90px;
    right: 0;
    width: 0;
    height: calc(100vh - 90px);
    //height: 90vh;
    padding-bottom: 150px;
    padding-top: 0;
    overflow-x: scroll;
    overflow-y: auto;
    z-index: 9991;
    //opacity: 0;
    background: $white;
    pointer-events: none;
    transition: all $long-transition;

    &.showed{
      width: 100vw;
      z-index: 9991;
      //opacity: 1;
      pointer-events: auto;
      transition: all $long-transition;
    }
  }

  .events-filter{
    .btn-close{
      display: block;
    }

    .btn-default{
      margin: 10px auto;
    }
  }

  #filter-venues, #filter-subcategories{
    .checkbox-label{
      &:hover{
        color: initial;
      }
    }
  }

  .events-filter{
    width: 100%;

    .heading{
      width: 100%;
      font-size: $f24px;
      background: $light-grey-2;
      padding: 21px 28px;
    }

    fieldset{
      &:last-child{
        .btn-group{
          display: flex;

          #reset-filter {
            width: auto;
            padding: 14px;
            background: transparent;
            color: $theme-color;

            &:hover{
              &:after{
                //display: none;
              }

              background: $white;
              box-shadow: none;
            }
          }

          #apply-filter{
            width: auto;
            padding: 14px 68px;
          }
        }
      }
    }

  }

  #filter-date{
    width: 270px;
    margin: 0 auto;
    display: block;
  }

  #filter-venues, #filter-subcategories{
    margin-bottom: 25px;

    .options{
      display: flex;
      width: 100%;
      flex-wrap: wrap;
      padding: 15px 25px;
    }

    .option{
      margin-top: 20px;
      width: 50%;
    }


    .checkbox-label{
      &:before{
        top: 0;
      }

      span{
        &:first-child{
          font-weight: $regular;
        }

        &:nth-child(2){
          color: $default-color;
          margin-top: 7px;
          font-size: $f14px;
        }
      }
    }
  }

  #filter-venues{
    .option{
      margin-bottom: 20px;
    }

  }
}

@media screen and (max-width: #{$screen-sm-max}) { // max-width: 768px

}

@media screen and (max-width: #{$screen-xs-max}) { // max-width: 590px
  #events-bar{
    top: 60px;
    height: calc(100vh - 60px);
    padding-bottom: 60px;
  }

  .events-filter{
    .heading{
      font-size: $f22px;
      padding: 17px 17px;
    }

    fieldset{
      &:last-child{

      }
    }

    .btn-default{
      width: 45%!important;
      padding: 14px 14px!important;
    }
  }

  #filter-venues, #filter-subcategories{
    margin-bottom: 0;

    .options{
      margin-bottom: 40px;
      padding: 0 17px;

      .option{
        width: 100%;
      }
    }
  }

  #filter-venues{
    margin-bottom: 0;
  }
}

@media screen and (max-width: #{$screen-xxs-max}) { // max-width: 320px
}