$main_max_width: 1140px;

.sh-procurements-page {
  .main-content {
    .content {
      padding: 66px 0px 140px 0px;
      > .title {
        font-family: $secondary-font;
        font-style: normal;
        font-weight: $bold;
        line-height: 58px;
        font-size: 48px;
        text-transform: uppercase;
        color: $green;
        margin: 0px 0 52px 0;
        text-align: center;
      }
      .procurements {
        .tabs {
          width: 100%;
          max-width: $main_max_width;
          border-bottom: 1px solid $form-border-color;

          margin: 0 auto 101px auto;
          display: flex;
          flex-direction: row;
          justify-content: flex-start;

          .tab {
            flex-grow: 1;
            width: 50%;
            text-align: center;
            text-transform: uppercase;
            cursor: default;

            .text-cont {
              display: flex;
              align-items: center;
              justify-content: center;
              width: 100%;
              height: 100%;
              position: relative;
              top: 1px;
              font-size: 24px;
              line-height: 28px;
              color: $black;
              font-family: $secondary-font;
              padding: 5px 0;
              transition: all .3s ease-in-out;
              border-bottom: 3px solid transparent;
              cursor: pointer;
            }

            &:hover, &.active {
              .text-cont {
                border-bottom: 3px solid $green;
              }
            }
          }
        }
        .tab-content {
          .items {
            .item {
              width: 703px;
              display: table;
              border: 1px solid #e0e0e0;
              margin: 0 auto 20px auto;
              color: $theme-color;
              font-size: 18px;
              line-height: 25px;

              > div {
                display: table-cell;
                vertical-align: middle;
                padding: 25px 40px;
              }
            }
          }
          .load-more-cont {
            margin-top: 118px;
            .devider {
              border-bottom: 1px solid $light-grey;
            }
          }

          &.tab-content_regular {
            .items {
              .item {
                &__date {
                  width: 1%;
                }

                &__title {
                  a {
                    text-decoration: underline;
                  }
                }

                &__url {
                  width: 1%;
                  text-align: center;
                  a {
                    display: block;
                    width: 28px;
                    height: 29px;
                    margin: 0 auto 6px auto;

                    .svg-file-logo {
                      fill: $green;
                      width: 28px;
                      height: 29px;
                    }
                  }
                  .filesize {
                    white-space: nowrap;
                    color: $geo-mark-color;
                    font-size: 14px;
                    display: block;
                  }
                }
              }
            }
          }

          &.tab-content_prozorro {
            .items {
              .item {
                &__url {
                  width: 43%;
                  a {
                    text-decoration: underline;
                    color: $green;
                    &:hover {
                      color: $theme-color;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: #{$screen-lg-max}) { // max-width: 1440px
}

@media screen and (max-width: #{$screen-md-max}) { // max-width: 1024px
}

@media screen and (max-width: #{$screen-sm-max}) { // max-width: 768px
  .sh-procurements-page{
    .main-content{
      .content {
        padding-top: 65px;
        padding-bottom: 65px;
        .container-fluid,
        .container-content{
          padding-right: 5%;
          padding-left: 5%;
        }
        h2{
          text-align: left;
          margin-bottom: 33px;
          font-style: normal;
          font-weight: bold;
          line-height: 58px;
          font-size: 42px;
          letter-spacing: 0.04em;
        }
        .tabs-wrapper {
          .tabs {
            flex-direction: column;
            margin-bottom: 67px;
            &.reversed{
              flex-direction: column-reverse;
            }
            .tab{
              width: 100%;
              display: none;
              position: relative;
              text-align: left;
              &:hover{
                .text-cont{
                }
              }
              .text-cont{
                padding: 10px 0 9px;
                justify-content: flex-start;
              }
              &.active{
                display: block;
                &:after{
                  content: '';
                  position: absolute;
                  top: 45%;
                  right: 0;
                  background: url("../images/icons/drop_down.svg") center no-repeat;
                  background-size: cover;
                  width: 12px;
                  height: 6px;
                }
              }
            }
          }
        }

        .procurements{
          .tab-content.tab-content_regular{
            .items{
              .item__date{
                padding: 23px 20px;
              }
              .item__title{
                padding: 23px 20px;
              }
              .item__url{
                padding: 10px 16px 4px;
              }
            }
          }
          .tab-content{
            .items{
              .item>div{
                padding: 23px 20px;
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: #{$screen-xs-max}) { // max-width: 590px
  .sh-procurements-page{
    .main-content{
      .content {
        padding-top: 40px;
        padding-bottom: 40px;
        h2{
          margin-bottom: 21px;
          font-size: 1.5em;
          line-height: 33px;
        }
        .tabs-wrapper {
          .tabs {
            margin: 0 auto 30px auto;
            .tab {
              .text-cont {
                font-style: normal;
                font-weight: normal;
                line-height: normal;
                font-size: 18px;
                letter-spacing: 0.02em;
                text-transform: uppercase;
              }
            }
          }
        }

        .procurements{
          .tab-content.tab-content_regular{
            .items{
              .item{
                width: 100%;
                .item__date{
                  font-style: normal;
                  font-weight: normal;
                  line-height: 22px;
                  font-size: 16px;
                  display: block;
                  padding: 17px 20px 0;
                  border-bottom: none;
                }
                .item__title{
                  font-style: normal;
                  font-weight: normal;
                  line-height: 22px;
                  font-size: 16px;
                  text-decoration-line: underline;
                  display: block;
                  padding: 4px 8px 20px 20px;
                  border-left: 1px solid #e0e0e0;
                  border-top: none;
                }
                .item__url{
                  padding: 10px 16px 4px;
                }
              }
            }
          }
          .tab-content_prozorro{
            .items{
              .item{
                width: 100%;
                >div{
                  display: block;
                  padding: 23px 20px 0;
                  font-style: normal;
                  font-weight: normal;
                  line-height: 22px;
                  font-size: 16px;
                }
                .item__url{
                  font-style: normal;
                  font-weight: normal;
                  line-height: 19px;
                  font-size: 14px;
                  text-decoration-line: underline;
                  color: #7AB317;
                  display: block;
                  padding: 4px 20px 20px;
                  width: 100%;
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: #{$screen-xxs-max}) { // max-width: 320px
}