.news{
  position: relative;
  padding: 55px 0 0;
  border-bottom: 1px solid #F2F2F2;

  &:before{
    content: '';
    position: absolute;
    z-index: -1;
    top: 218px;
    display: block;
    background: #144084;
    width: 100%;
    height: 236px;
  }

  &.category{
    padding: 55px 0;
    border: none;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    &:before{
      display: none;
    }

    h1{
      font-family: $secondary-font;
      font-style: normal;
      font-weight: bold;
      line-height: normal;
      font-size: 48px;
      text-align: center;
      text-transform: uppercase;
      color: #7AB317;
      margin: 12px 0 46px;
      width: 100%;
    }
  }

  .container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;

    .wrap{
      display: flex;
      flex-wrap: wrap;
    }

    .post_box{
      display: inline-block;
      position: relative;
      min-width: 340px;
      width: 340px;
      background: #FFFFFF;
      box-shadow:
              0 7px 35px rgba(90, 97, 105, 0.1),
              0 15px 22px rgba(90, 97, 105, 0.1),
              0 4px 8px rgba(90, 97, 105, 0.12),
              0 2px 3px rgba(90, 97, 105, 0.11);
      margin: 35px 0;

      .post_image{
        height: 237px;
      }
      .post_labels{
        display: flex;
        justify-content: space-between;
        padding: 32px 34px 16px;

        .label{
          font-style: normal;
          font-weight: normal;
          line-height: normal;
          font-size: 14px;
          color: #FFFFFF;
          background: #7AB317;
          border: 1px solid #7AB317;
          box-sizing: border-box;
          border-radius: 5px;
          padding: 8px 11px 7px 14px;
          max-width: 70%;
          overflow: hidden;
          text-overflow: ellipsis;
        }
        .date{
          font-family: $secondary-font;
          font-style: normal;
          font-weight: normal;
          line-height: normal;
          font-size: 16px;
          color: #282828;
          padding: 4px 8px;
        }
      }

      h3{
        font-weight: 500;
        line-height: 35px;
        //font-size: 28px;
        letter-spacing: -0.491228px;
        color: #282828;
        padding: 0 34px;
      }

      .exc{
        font-style: normal;
        font-weight: normal;
        line-height: 19px;
        font-size: 14px;
        color: #282828;
        padding: 17px 34px 40px;
      }

      /* new hover effect*/
      .simple-slider-hover {
        content: '';
        @extend .absolute;
        top: -30px;
        left: -30px;
        right: -30px;
        bottom: -30px;
        visibility: hidden;
        opacity: 0;
        background-color: rgba(255, 255, 255, 0.2);
        z-index: -1;
        transform: translate3d(0, 0, 0) scale(0.8);
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
        transition: all .3s;
      }

      &:hover {
        box-shadow: none;
        & > .simple-slider-hover {
          visibility: visible;
          opacity: 1;
          transform: translate3d(0, 0, 0);
        }
      }
    }
  }

  h2{
    //font-size: 36px;
    width: 100%;
    color: $theme-color;
  }

}

.top_news{
  //min-height: 100vh;
  .container{
    display: flex;
    align-items: center;
    padding: 53px 0 53px 80px;
    .left{
      width: 50%;
      padding-right: 56px;
      .category{
        font-family: $secondary-font;
        font-style: normal;
        font-weight: normal;
        line-height: normal;
        font-size: 18px;
        text-transform: uppercase;
        color: #0B2E59;
      }
      h1{
        font-style: normal;
        font-weight: bold;
        line-height: 61px;
        font-size: 48px;
        letter-spacing: 0.02em;
        text-transform: uppercase;
        color: #7AB317;
        margin: 10px 0 37px;
      }
      .excerpt{
        font-style: normal;
        font-weight: normal;
        line-height: 29px;
        font-size: 18px;
        color: #282828;
        p{
          line-height: 29px;
        }
      }

    }
    .right{
      width: 50%;
      max-height: 510px;
      overflow: hidden;
      .img{
        img{
          width: 100%;
        }
      }

    }
  }
}

.main_news{
  padding: 70px 0;
  .container{
    padding-left: 80px;
    h2{
      font-family: $secondary-font;
      font-style: normal;
      font-weight: normal;
      line-height: 50px;
      font-size: 36px;
      letter-spacing: 0.02em;
      text-transform: uppercase;
      color: #0B2E59;
      margin: 57px 0 55px;
    }
    .content{
      font-style: normal;
      font-weight: normal;
      line-height: 29px;
      font-size: 18px;
      color: #282828;
      p{
        margin: 0 0 29px;
        line-height: 29px;
      }
    }
  }
}

@media screen and (max-width: #{$screen-lg-max}) { // max-width: 1440px
}

@media screen and (max-width: #{$screen-md-max}) { // max-width: 1024px
  .news{
    h2{

    }
  }
}

@media screen and (max-width: #{$screen-sm-max}) { // max-width: 768px
  .news{
    &.category{
      h1{
        line-height: 58px;
        font-size: 42px;
        letter-spacing: 0.04em;
        margin: 47px 0 21px;
      }
    }
    .container{
      @include container(93.5%);

      .post_box{
        width: 348px;
        .post_labels {
          padding: 35px 30px 20px;
        }
      }
    }
  }
}

@media screen and (max-width: #{$screen-xs-max}) { // max-width: 590px
  .news {

    h2{
      text-align: center;
    }

    &.category {
      h1 {
        font-size: 24px;
        letter-spacing: 0.02em;
        margin: -5px 0;
        line-height: 32px;
      }
    }
    .container{
      @include container(93.5%);
      justify-content: center;

      .wrap{
        width: 343px;
        margin: 0 auto;
        display: flex;
        flex-wrap: wrap;
      }

      .post_box{
        //display: block!important;
        min-width: 0!important;
        width: 348px;
        padding-bottom: 34px;
        margin-bottom: 9px;
        margin-left: 10px!important;
        margin-right: 10px!important;
        .post_labels {
          padding: 24px 24px 16px;
          .label{
            padding: 6px 11px 5px 14px;
          }
          .date{
            font-size: 14px;
            padding: 8px 0 5px;
          }
        }
        .exc{
          display: none;
        }
        h3{
          font-style: normal;
          font-weight: normal;
          line-height: 30px;
          font-size: 22px;
          letter-spacing: 0.02em;
          text-transform: uppercase;
          padding: 0 24px;
        }
      }
    }
  }
}

@media screen and (max-width: #{$screen-xxs-max}) { // max-width: 320px
}